@import "styles/_media.scss";
@import "styles/_variables.scss";
@import "styles/_fonts.scss";

body {
    color: #fff;
    font-family: arial, sans-serif;
    overscroll-behavior-y: none;
    overflow-x: hidden;
    font-family: "Mulish";
    background-color: black;
    touch-action: pan-x pan-y;

    @include md {
        overflow: hidden;
    }

    #root,
    #root > div,
    #root > div > div {
        height: 100%;
    }
}

a {
    text-decoration: none;
}

ul {
    padding: 0;
}

.appcontainer {
    background-color: $accent-medium;
    width: 100%;
    height: 100vh;
    padding: 0;
}

.workspace,
.navigation {
    padding: 10px 0;
}

.contentcontainer {
    background: black;
    height: 100%;
    padding: 14px;

    @include md {}
}

label {
    display: block;
}

.inline {
    float: left;
}

.btn-group button {
    border: 1px solid black;
    /* Green border */
    cursor: pointer;
    /* Pointer/hand icon */
    display: block;
    /* Make the buttons appear below each other */
}

.btn-group button:not(:last-child) {
    border-bottom: none;
    /* Prevent double borders */
}

/* Add a background color on hover */
.btn-group button:hover {
    background-color: #3e8e41;
}

.close {
    position: absolute;
    font-size: x-large;
    right: 15px;
    top: 7.5px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
    background-color: 255, 255, 255;
}

.close:hover {
    opacity: 1;
}

.close:before,
.close:after {
    position: absolute;
    left: 15px;
    content: " ";
    height: 33px;
    width: 2px;
}

/* Style the tab */
.tab {
    padding-right: 12px;
    z-index: 4;
    position: fixed;
    background-image: linear-gradient(to right, AliceBlue, Azure);
    max-width: inherit;
    width: 100%;
    border: 1px solid DimGray;
}

/*tab + div { }
tab + select { padding-top: 50px; } */

/* Style the buttons inside the tab */
.tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
    background-color: LightSteelBlue;
}

/* Create an active/current tablink class */
.tab button.active {
    background-color: LightBlue;
}

/* Style the tab content */
.tabcontent {
    margin-top: 50px;
    display: none;
    border: 1px solid LightSlateGray;
    border-top: none;
    border-right: none;
}

